<script>
import MessageNotification from '@/components/MessageNotification.vue'

export default {
  name: 'App',
  components: {
    MessageNotification
  },
  data() {
    return {
      isCollapsed: false,
      activeMenu: '/',
      expandedMenus: ['lastmile', 'inventory', 'overseas', 'supplier', 'supplier-order', 'supplier-quote', 'supplier-rating', 'admin', 'admin-contract', 'supplier-appointment', 'work-order', 'admin-tag', 'admin-tag-system']
    }
  },
  methods: {
    toggleCollapse() {
      this.isCollapsed = !this.isCollapsed
    },
    toggleSubmenu(menuKey) {
      const index = this.expandedMenus.indexOf(menuKey)
      if (index === -1) {
        this.expandedMenus.push(menuKey)
      } else {
        this.expandedMenus.splice(index, 1)
      }
    },
    navigateTo(path) {
      this.activeMenu = path
      this.$router.push(path)
    }
  },
  created() {
    this.activeMenu = this.$route.path
  }
}
</script>

